#addUser {
	position: fixed;
	left: 1rem;
	top: 1rem;
	text-align: center;
}

#addUser label {
	font-size: 1.5rem;
}

#addUser input {
	width: 100px;
}

#users {
	position: fixed;
	right: 1rem;
	top: 1rem;
	text-align: center;
	background-color: rgba(239, 126, 237, 0.434);
	height: 2.5rem;
	font-size: 1.25rem;
	font-weight: 500;
	font-family: Avenir, Helvetica, Arial, sans-serif;
}

#scheduleInput {
	text-align: center;
}

#scheduleInput label {
	font-weight: 600;
	font-style: italic;
	display: inline-block;
	width: 150px;
	text-align: right;
	font-size: 1.5rem;
}

form {
	display: inline-block;
}

input {
	margin-left: 1rem;
	border: none;
	-webkit-appearance: none;
	-ms-appearance: none;
	-moz-appearance: none;
	appearance: none;
	background: #a6f5fb4c;
	padding: 12px;
	border-radius: 3px;
	width: 250px;
	font-size: 1.25rem;
}

button {
	border: none;
	-webkit-appearance: none;
	-ms-appearance: none;
	-moz-appearance: none;
	appearance: none;
	margin-top: 1rem;
	margin-left: 1rem;
	background-color: rgba(236, 132, 241, 0.144);
	border-radius: 0.25rem;
	font-size: 1.5rem;
	box-shadow: 2px 2px 8px rgba(80, 45, 82, 0.947);
}

button:hover {
	background-color: rgba(243, 155, 248, 0.583);
}
